<template>
  <div class="goods">
    <ul class="list">
      <li class="list-item" v-for="item in list" :key="item.id">
        <div class="item-con">
          <div class="imgbox">
            <img :src="item.image" alt="item.name" />
          </div>
          <div class="name">{{ item.name }}</div>
          <div class="info">
            <div class="price_current">
              {{ item.price_current }}
              <span>元</span>
            </div>
            <div class="price_before">原价：￥{{ item.price_before }}</div>
          </div>
          <div class="btn">{{ item.btn }}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["list"]
};
</script>

<style lang="scss" scoped>
.goods {
  margin-top: 20px;
  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 20px;
    .list-item {
      width: 50%;
      margin-bottom: 14px;
      &:nth-of-type(odd) {
        .item-con {
          margin-right: 7px;
        }
      }
      &:nth-of-type(even) {
        .item-con {
          margin-left: 7px;
        }
      }
      .item-con {
        background-color: #fff;
        position: relative;
        .imgbox {
          img {
            width: 100%;
            border-radius: 10px 10px 0px 0px;
          }
        }
        .name {
          height: 76px;
          margin: 6px 18px 0 14px;
          color: #333;
          font-size: 28px;
        }
        .info {
          padding: 0px 0px 26px 14px;
          .price_current {
            color: #ff4a54;
            font-size: 36px;
            margin-bottom: 10px;
            span {
              font-size: 26px;
            }
            
          }
          .price_before {
            color: #888888;
            font-size: 22px;
          }
        }
        .btn {
          position: absolute;
          color: #fff;
          font-size: 24px;
          width: 130px;
          height: 56px;
          background-color: #ff4a54;
          border-radius: 28px;
          text-align: center;
          line-height: 56px;
          right: 14px;
          bottom: 42px;
        }
      }
    }
  }
}
</style>



